@import url(https://fonts.googleapis.com/css2?family=Tourney:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kelly+Slab&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.homepage {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.homepage .top {
  padding: 10px;
  padding-left: 50px;
}
.homepage .top h1 {
  font-size: 2rem;
  font-family: "Tourney", cursive;
  color: rgba(87, 83, 83, 0.76);
}

.homepage .content {
  flex: 1 1;
  display: flex;
}

.homepage .content .intro {
  margin: 20px auto;
}

.heading-container{
  /* border: 1px solid red; */
  text-align: center;
}

.heading-container h1{
  font-family: 'Kelly Slab', cursive;
  font-size: 2rem;
}

.heading-container span{
  margin: 5px;
}

.heading-container span a{
  font-size: 1rem;
  text-decoration: none;
  color: rgba(0, 0, 255, 0.664);
}

.heading-container span:hover{
  border-bottom: 2px solid #888;
}

.heading-container h4{
  color: rgb(78, 75, 75);
  font-size: 1rem;
}
.search .searchbox {
  /* border: 1px solid red; */
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.search .searchbox input {
  height: 30px;
  width: 300px;
}

.result-container{
  /* border: 1px solid red; */
  height: 700px;
  padding: 15px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  overflow-y: scroll;
  grid-gap: 20px;
  gap: 20px;
}

.result-container .card{
  border: 1px solid lightgray;
  height: 300px;
  width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.result-container .result-img{
  height: 180px;
  width: 240px;
  margin: 1rem;
  background-position: center;
}

.result-container .card .card-detail{
  background-color: lightgray;
  /* border: 1px solid red; */
  width: 100%;
  height: 50px;
  display: flex;
  flex-direction: column;
}
.trending {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.trending .top {
  padding: 50px;
}

.trending .top a {
  text-decoration: none;
  font-size: 1rem;
  text-transform: uppercase;
  color: rgba(0, 0, 255, 0.664);
}

.trending .content{
  flex: 1 1;
}
